import React, { useEffect } from "react";
import { motion, useAnimation } from "framer-motion";
import { useInView } from "react-intersection-observer";

function Features() {
  const { ref, inView } = useInView();
  const animation = useAnimation();

  useEffect(() => {
    console.log(inView);
    if (inView) {
      animation.start({
        x: 0,
        transition: {
          type: "spring",
          duration: 1,
          bounce: 0,
        },
      });
    }

    if (!inView) {
      animation.start({
        x: "-100vw",
      });
    }
  }, [inView]);

  return (
    <div>
      <section class="hero text-gray-600 body-font  px-16 py-1 ">
        <div
          class="container mx-auto flex  md:flex-row flex-col items-center"
          style={{ height: "100%" }}
        >
          <div class="about-banner lg:max-w-lg lg:w-full md:w-1/2 w-5/6 ">
            {/* class="object-cover object-center rounded" */}
          </div>
          <div class="lg:flex-grow md:w-1/2 lg:pl-24 md:pl-16 flex flex-col md:items-start md:text-left items-center text-center">
            <h1 class="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900">
              Skills
            </h1>
            <p class="mb-8 leading-relaxed">
              I can work in HTML, CSS, Javascript and python.<br></br> I have worked with frameworks like ReactJS, NextJS, Express.<br></br> I have also worked in databases such as MongoDB, MySQL.
            </p>
            <div class="flex justify-center">
            </div>
          </div>
        </div>
        <div id="team"></div>
      </section>
    </div>
  );
}

export default Features;